<template>
  <div>
    <!--  -->
    <el-card class="circular">
      <div slot="header" class="clearfix">
          <div class="title-blue-bar"></div>
          <div class="card-title"></div>
          <div class="header-btn" >
            <el-button size="mini" type="primary" @click="exportFile" ></el-button>
          </div>
          
      </div>
      <el-form  label-width="140px">
            <el-row>
              <el-row>
                <el-col :span="8">
                  <el-form-item label=":" class="text-left">
                    <el-date-picker
                      value-format="yyyy-MM-dd"
                      v-model="summaryForm.startDate"
                      type="date"
                      placeholder=""
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":" class="text-left">
                    <el-date-picker
                      value-format="yyyy-MM-dd"
                      v-model="summaryForm.endDate"
                      type="date"
                      placeholder=""
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":" class="text-left">
                    <el-radio-group v-model="summaryForm.state" @change="changeRadioHandler">
                      <el-radio label="1"></el-radio>
                      <el-radio label="2"></el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-form-item label=":" class="text-left">
                  <el-radio-group
                   @change='chooseControl("one")'
                    v-model="summaryForm.levelOneBusinessType"
                  >
                    <el-radio
                      v-for="state in status1"
                      :label="state.code"
                      :key="state.code"
                     
                    >{{state.value}}</el-radio>
                  </el-radio-group>
                </el-form-item>
              </el-row>
              <el-row>
                <el-form-item label=":" class="text-left">
                  <el-radio-group
                  @change='chooseControl("two")'
                    v-model="summaryForm.levelTwoBusinessType"
                  >
                    <el-radio

                      v-for="state in status2"
                      :label="state.code"
                       
                      :key="state.code"
                    >{{state.value}}</el-radio>
                  </el-radio-group>
                </el-form-item>
              </el-row>
              
            </el-row>
          </el-form>

    </el-card>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import utils from '../../../utils'


export default {
  name: "underwritingSummaryForm",
  components: {
    
  },
  data() {
    return {
      status1: [
        { code: "0", value: "" },
        { code: "1", value: "" },
        { code: "2", value: "" },
        { code: "3", value: "" },
        { code: "4", value: "" },
        { code: "5", value: "/" },
        { code: "6", value: "" },
        { code: "7", value: "" },
        { code: "8", value: "" },
        { code: "9", value: "" },
        { code: "10", value: "/" },
        { code: "11", value: "" },
        { code: "12", value: ""}
        
      ],
      status2: [
        { code: "0", value: "" },
        { code: "1", value: "C" },
        { code: "2", value: "" },
        { code: "3", value: "" },
        { code: "4", value: "" },
        { code: "5", value: "" }
      ],
      summaryForm: {
        state: "1",
        businessStates: [],
        levelOneBusinessType: '',
        levelTwoBusinessType:'',
      },
      
    };
  },

  computed: {
    ...mapGetters(["getList"])
  },

  methods: {
    exportFile() {
      let url = this.HOST + this.$url.exportSpeciallyApprovedMaintain
      utils.axiosDown(url,this.summaryForm)
    },
    // 
    changeRadioHandler(){
        console.log(this.summaryForm.state,typeof this.summaryForm.state)
        if(this.summaryForm.state == 1){
            this.status1.push({ code: "12", value: ""})
        }else if(this.summaryForm.state == 2){
           this.status1.pop({ code: "12", value: ""})
        }
    },

    chooseControl(level){
      console.log(level)
      switch (level) {
        case 'one':
          this.summaryForm.levelTwoBusinessType = ''
          break;
      
        case 'two':
          this.summaryForm.levelOneBusinessType = ''
          break;
      }
    }
   
  },
  created() {}
};
</script>
<style scoped>
.card-title {
  color: #717385;
  font-size: 15px;
  font-weight: 700;
  text-align: left
}
.el-card >>> .el-card__body {
  padding: 10px 20px;
}
.title-blue-bar {
  width: 10px;
  height: 20px;
  background-color: #409eff;
  border-radius: 8px;
  margin-right: 10px;
  float: left
}
.circular {
  border-radius: 8px;
}
.header-btn{
  position: absolute;
  right: 35px;
  top: 10px;
}
.el-card >>> .el-card__header{
  border: none
}

</style>
