<template>
  <div>
    <el-collapse-item :name="title" class="noArrow">
      <template slot="title">
        <i class="el-icon-arrow-right pr5" v-show="!arrow"></i>
        <i class="el-icon-arrow-down pr5" v-show="arrow"></i>
        {{title}}
      </template>
      <el-row class="text-left">
        <el-pagination
          class="mb10 mt10"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :pager-count="5"
          small
          :current-page="1"
          :page-sizes="[10, 20, 50]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </el-row>
      <el-table
        stripe
        :data="results"
        tooltip-effect="dark"
        :cell-style="{'text-align': 'center'}"
        :header-cell-style="{'text-align': 'center'}"
        :header-cell-class-name="'table-header-bg'"
      >
        <el-table-column prop="businessType" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="text" size="mini" @click="goDetail(scope.row)">{{scope.row.businessNo}}</el-button>
          </template>
        </el-table-column>
        <el-table-column v-if="title == ''" prop="businessNo" label=""></el-table-column>
        <el-table-column v-if="title != ''" prop="licenseNo" label=""></el-table-column>
        <el-table-column v-if="title != ''" prop="channelAgentName" label=""></el-table-column>
        <el-table-column v-if="title != ''" prop="insuredName" width="100" label=""></el-table-column>
        <el-table-column prop="comName" label=""></el-table-column>
        <el-table-column prop="taskLevel" label=""></el-table-column>
        <el-table-column prop="inDate" label=""></el-table-column>
        <el-table-column v-if="title != ''" prop="startDate" label="">
          <template slot-scope="scope">
            {{ scope.row.startDate | filterDate}}
          </template>
        </el-table-column>
        <el-table-column prop="operatorCode" label=""></el-table-column>
        <el-table-column v-if="title != ''" prop="costRate" width="115" label=""></el-table-column>
        <el-table-column v-if="title != ''" prop="marketFeeRate" width="90" label=""></el-table-column>
        <el-table-column v-if="title != ''" prop="agentCode" label=""></el-table-column>
        <el-table-column prop="ruleNo" width="100" label=""></el-table-column>
      </el-table>
    </el-collapse-item>
  </div>
</template>
<script>
export default {
  props: {
    //
    results: {
      type: Array
    },
    //
    title: {
      type: String
    },
    //
    total: {
      type: Number
    },
    //
    pageSize: {
      type: Number,
      default: () => {
        return 10;
      }
    },
    //
    arrow: {
      type: Boolean
    },
    ///
    mark: {
      type: String
    }
  },

  methods: {
    // 
    handleSizeChange(val) {
      this.$emit("paging", {
        mark: this.mark,
        title: this.title,
        pageSize: val
      });
    },
    // 
    handleCurrentChange(val) {
      this.$emit("paging", {
        mark: this.mark,
        title: this.title,
        pageNo: val
      });
    },
    goDetail(row) {
      let key ={
        title : this.title
      }
      this.$emit('goDetail', {
        ...row,
        ...key})
    }
  }
};
</script>
<style scoped>
.el-collapse-item >>> .el-collapse-item__header {
  border-bottom: 1px solid #ebeef5;
}
</style>


