<template>
  <div>
    <!--  -->
    <el-card class="circular">
      <el-collapse v-model="activeNames">
        <el-collapse-item name="1">
          <template slot="title">
            <div class="title-blue-bar"></div>
            <div class="card-title"></div>
          </template>
          <el-form ref="form" :model="underwriterInfor" label-width="140px">
            <el-row>
              <el-row>
                <el-col :span="8">
                  <el-form-item label=":">
                    <el-input v-model="underwriterInfor.usercode"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":">
                    <el-select v-model="underwriterInfor.vaild" clearable placeholder="">
                      <el-option
                        v-for="(item,index) in vaildOptions"
                        :key="index"
                        :label="item.value + '-'+item.label"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":">
                    <el-input v-model="underwriterInfor.userName"></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="8">
                  <el-form-item label=":">
                    <el-input v-model="underwriterInfor.phonecode"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":" class="text-left">
                    <el-input v-model="underwriterInfor.telephone"></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-col :span="24" class="text-center">
                <el-button @click="query()" size="mini" type="primary"></el-button>
                <el-button @click="enterWriterInfor('ADD',underwriterInfor.usercode)" size="mini"></el-button>
              </el-col>
            </el-row>
          </el-form>
        </el-collapse-item>
      </el-collapse>
    </el-card>
    <!--  -->
    <el-card class="circular mt4 shadow">
      <!-- <el-pagination
        class="mb10 mt10 text-left"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :pager-count="5"
        small
        :current-page="1"
        :page-sizes="[10, 20, 50]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="results.length"
      ></el-pagination> -->
      <el-table
        stripe
        :data="results"
        tooltip-effect="dark"
        :cell-style="{'text-align': 'center'}"
        :header-cell-style="{'text-align': 'center'}"
        :header-cell-class-name="'table-header-bg'"
      >
        <el-table-column prop="userName" label=""></el-table-column>
        <el-table-column prop="userCode" label=""></el-table-column>
        <el-table-column prop="mailbox" label=""></el-table-column>
        <el-table-column prop="phonecode" label=""></el-table-column>
        <el-table-column prop="telephone" label=""></el-table-column>
        <el-table-column prop="autoDistributeFlag" label=""></el-table-column>
        <el-table-column prop="autoPushNum" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="enterWriterInfor('CHANGE',scope.row.usersCode)"
            ></el-button>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="deletes(scope.row.usersCode)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--   -->
    <el-dialog title="" class="zhuxiao" :visible.sync="centerDialogVisible" width="30%" center>
      <span>???</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confrimCancle"> </el-button>
        <el-button @click="centerDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { relations } from "@/assets/js/baseCode";
import { type } from 'os';

export default {
  name: "underwriterInfor",

  data() {
    return {
      centerDialogVisible:false,
      activeNames: ["1"],
      vaildOptions:[
        {value: '0',label: ''},
        {value: '1',label: ''},
      ],
      pageSize: 10,
      valueidx:"",
      userCode: '',
      underwriterInfor: {},
      results:[]
    };
  },

  computed: {
    ...mapGetters(["getList"])
  },

  methods: {
    ...mapActions(["getunderwriterInfor"]),

    reset() {
      this.$router.push({
        name: ""
      });
    },
    enterWriterInfor(type, code) {
      // console.log(idx,code)
      this.$router.push({path:'/addwriteInfor',query:{
        'type': type,
        'userCode': code
      }})
      // this.$router.push({
      //   path: "/changewriterInfor",
      //   query: {
      //     idx,
      //     code
      //   }
      // });
    },
    deletes(code) {
      this.userCode = code;
      this.centerDialogVisible=true;
      // row.splice(idx + 1, 1);
    },
    confrimCancle(){
      this.$fetch.post(this.HOST + this.$url.underWriterInforDeleteChechker,{ 'sercode':this.userCode}),then(data =>{
        this.query()
      })
    },
    query() {
      // this.getunderwriterInfor(this.underwriterInfor);
      this.$fetch.post(this.HOST + this.$url.underWriterInforGetCkeckerList,this.underwriterInfor).then( data => {
        this.results = data
      })
    },

    handleSizeChange(val) {
      this.task.tab1 = val;
    },

    handleCurrentChange(val) {
      this.task.tab2 = val;
    }
  },
  created() {}
};
</script>
<style scoped>
 .zhuxiao >>> .el-dialog--center .el-dialog__body{
  text-align: center;
  font-size: 20px;
}

</style>
