<template>
  <div>
    <!--  -->
    <el-card class="circular">
      <el-collapse v-model="activeNames">
        <el-collapse-item name="1">
          <template slot="title">
            <div class="title-blue-bar"></div>
            <div class="card-title"></div>
          </template>
          <el-form
            :disabled="flag == 'detail'"
            ref="form"
            :rules="form"
            :model="correction"
            label-width="80px"
          >
            <el-row>
              <el-form-item class="text-left">
                <el-checkbox-group
                  class="inline-block"
                  v-model="companyCodes"
                  @change="handleCheckedCitiesChange"
                >
                  <el-checkbox
                    v-for="company in companies"
                    :label="company"
                    :key="company"
                  >{{company}}</el-checkbox>
                </el-checkbox-group>
              </el-form-item>
              <el-col :span="24" class="text-center">
                <el-button @click="autoPushQuery()" size="mini" type="primary" :disabled="disableds"></el-button>
                <el-button @click="stopPush" size="mini" :disabled="!disableds"></el-button>
              </el-col>
            </el-row>
          </el-form>
        </el-collapse-item>
      </el-collapse>
    </el-card>
    <el-card class="circular">
      <el-collapse v-model="activeNames">
        <el-collapse-item name="1">
          <template slot="title">
            <div class="title-blue-bar"></div>
            <div class="card-title"></div>
          </template>
          <el-row>
            <el-col :span="24">
              <span class="spanStyle"></span>
              <br>
              <hr>
            </el-col>
          </el-row>
          <el-row class="text-left">
            <el-pagination
          class="mb10 mt10"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :pager-count="5"
          small
          :current-page="pageNo"
          :page-sizes="[10, 20, 50]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
            ></el-pagination>
          </el-row>
          <el-table
            :data="businessList"
            stripe
            tooltip-effect="dark"
            style="width: 100%"
            :cell-style="{'text-align': 'center'}"
            :header-cell-style="{'text-align': 'center'}"
            :header-cell-class-name="'table-header-bg'"
          >
            <el-table-column align="center" label="" prop="businessSates"></el-table-column>
            <el-table-column align="center" prop="businessNo" label="">
              <template slot-scope="scope">
                <el-button
                  type="text"
                  size="mini"
                  @click="godetail(scope.row)"
                >{{scope.row.businessNo}}</el-button>
              </template>
            </el-table-column>
            <el-table-column align="center" prop="carNumber" label=""></el-table-column>
            <el-table-column align="center" prop="channelName" label=""></el-table-column>
            <el-table-column align="center" prop="insuredName" label=""></el-table-column>
            <el-table-column align="center" prop="riskName" label=""></el-table-column>
            <el-table-column align="center" prop="companyCode" label=""></el-table-column>
            <el-table-column align="center" label="" prop="taskLevel"></el-table-column>
            <el-table-column align="center" prop="subTime" label=""></el-table-column>
            <el-table-column align="center" prop="handler" label=""></el-table-column>
            <el-table-column align="center" prop="feesroportion" label=""></el-table-column>
            <el-table-column align="center" prop="repairCode" label=""></el-table-column>
          </el-table>
        </el-collapse-item>
      </el-collapse>
    </el-card>
  </div>
</template>
<script>
import { relations } from "@/assets/js/baseCode";
let [inputValidator, selectValidator] = [
  { required: true, message: "", trigger: "blur" },
  { required: true, message: "", trigger: "change" }
];

export default {
  name: "autoPush",

  props: {
    flag: {
      type: String
    }
  },

  data() {
    return {
      businessList: [
        {
          businessSates: '' ,//	
          businessNo: '12322123' ,//	
          carNumber: 'B123122' ,//	
          channelName: 'free' ,//	
          insuredName: 'wpc' ,//	
          riskName: '' ,//	
          companyCode: '' ,//	
          taskLevel: 'AAA' ,//	
          subTime: '2019-09-02 23:23:23' ,//	
          handler: '' ,//	
          feesroportion: '3.004%' ,//	
          repairCode: 'A1232' ,//	
        }
      ],
      total: 0,
      pageNo: 1,
      pageSize: 10,
      disableds: true,
      companyCodes: [],
      companies: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      underwriting: {},
      fileList: [],
      relationsss: [
        { value1: "" },
        { value1: "1_" },
        { value1: "2_" }
      ],
      categoryss: [
        { value: "" },
        { value: "1_" },
        { value: "2_" },
        { value: "3_" }
      ],
      rtReported: {},
      flagss: [{ value: "" }, { value: "0_" }, { value: "1_" }],
      relations,
      activeNames: ["1"],
      correction: {},
      results: [],
      form: {},
      outerVisible: false
    };
  },

  computed: {},

  methods: {
    //
    goBack() {
      this.$router.go(-1);
    },
    handleClose(done) {
      console.log("");
    },
    //  
    autoPushQuery() {
      let keyWords = {
        companyCodes:this.companyCodes,
        pageSize:this.pageSize,
        pageNo: this.pageNo
      }
      this.$fetch.post( this.HOST + this.$url.autoDistributeSelectTaskList,keyWords).then(data => {

        this.businessList = data.businessList
        this.total = data.total
        this.$forceUpdate() // 
      })
    },
    godetail(row) {
      debugger
      this.$router.push({
        path: "/underwritingDetails",
        query: row
      });
    },
    reset() {},
    stopPush() {},
    handleCheckedCitiesChange(value) {
      console.log(value);
      if (value.length > 0) {
        this.disableds=false
      }else{
        this.disableds=true
      }
      let checkedCount = value.length;
      checkedCount === this.companies.length;
      checkedCount > 0 && checkedCount < this.companies.length;
    },

    submitUpload() {
      this.$refs.upload.submit();
    },
    //  
    handleCurrentChange(value){
      console.log(value)
      this.pageNo = value
      this.autoPushQuery()
    },
    //  
    handleSizeChange(value){
      console.log(value)
      this.pageSize = value
      this.autoPushQuery()
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },

    save() {
      this.outerVisible = true;
      // this.getcorrection(this.correction);
    }
  },
  created() {}
};
</script>
<style scoped>
.fontSizeTrue {
  font-size: 25px;
}
.dialog-footer-parent >>> .el-dialog__footer {
  text-align: center;
}
.spanStyle {
  color: #717385;
  font-size: 14px;
  font-weight: 700;
  float: left;
  margin-left: 20px;
}
</style>
