let loadingService

router.beforeEach((to, from, next) => {
  // loadoing
  loadingService = Loading.service({
    lock: true,
    text: 'Loading',
    customClass: 'loading-background'
  })

  next();
})

router.afterEach((to, from) => {
  // loading
  loadingService.close()
})

