<script>
import { mapActions, mapGetters } from "vuex";
import { setTimeout } from "timers";

export default {
  name: "detailedInformation",
  data() {
    return {
      subOptions: [],
      bussinessType: "",
      routeDate: "",
      activeNames: [],
      underwritingDetails: {
        taskProcessing: {}, //
        summaryInfo: {}, //
        underwriteOpinion: [], // 
        uwpTexts: {}, // 
        uwinsuredT: {}, // 
        uwinsuredI: {}, // 
        uwinsuredC: {}, // 
        uwitemCar: {}, // 
        uwcarmodel: {}, // 
        insurancePeriod: {}, // 
        uwitemkindZ: [], // 
        uwitemkindF: [], // 
        payTravelTaxInfo: {}, //
        uwqfixationDetails: [], // 
        uwcardrivers: [], // 
        uwengages: [], // 
        generalInformation: {}, // 
        otherInformation: {}, // 
        uwitemkindCount: {}, // 
        uwNotion: {}, // 
        displayFlag: {
          browseFlag: "0", // 
          flowRecordFlag: "0", //	
          ClaimFlag: "0", //	
          lastPolicyClaimFlag: "0", //	
          coverFacEnquiryFlag: "0", //	
          headOfficeInfoFlag: "0", //	
          branchOfficeInfoFlag: "0", //	
          relationImageFlag: "0", //	
          infoFlag: "0", //	
          uploadImageFlag: "0", //	
          mobileImageFlag: "0", //	
          deviceInfoFlag: "0", //	
          shipInfoFlag: "0", //	
          lastPolicyInfoFlag: "0", //	
          revokeFlag: "0", //	
          riskTypeRateFlag: "0", //	
          reportFormsFlag: "0", //	
          possessTaskFlag: "0", // 
          approvalInfoFlag: "0" //	
        },
        uwprofitdetails: [] //  -- 
      },
      //  ---
      uwNotionFlags: [
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" }
      ],
      //  ---
      uwNotionCarCheckStatus: [
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" },
        { value: "4", label: "" },
        { value: "5", label: "" }
      ],
      tableList: [{}],
      value: "",
      relationDialog: false,
      form: {},
      rules: {},
      radio: 1,
      tableData: [], //   
      outerVisible: false,
      innerVisible: false,
      adjustRateDialog: false, //  
      textarea1: "",
      condition: "0",
      textarea2: "",
      options: [
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        }
      ]
    };
  },

  methods: {
    //collapse
    setActiveNames() {
      for (let i = 1; i <= 23; i++) {
        this.activeNames.push(JSON.stringify(i));
      }
    },
    // 
    goback() {
      this.$router.go(-1);
    },

    init() {
      let keyWords = {
        request: "INFO", // 
        businessNo: this.routeDate.businessNo, // 
        taskType: this.routeDate.type, // 
        taskId: this.routeDate.businessNo, //  Id
        businessType: this.routeDate.type, // businessType
        comCode: this.routeDate.businessNo, // 
        userCode: this.routeDate.businessNo, // 
        username: "wpc"
      };
      this.$fetch
        .post(this.HOST + this.$url.uwmainGetUwInfo, keyWords)
        .then(data => {
          console.log(data);
          this.underwritingDetails = data;
          this.underwritingDetails.uwNotion = data.uwnotions[0];
          //  this.underwritingDetails.displayFlag ={}
        });
    },
    // 
    goTolinks(type) {
      let key = {};
      switch (type) {
        // 
        case "endor":
          key = {
            businessNo: this.routeDate.businessNo || "454654564564",
            businessType: this.routeDate.type || "sfsdfsdf",
            userName: "123",
            userCode: "123",
            comCode: "13000000"
          };
          this.$fetch
            .post(this.HOST + this.$url.autoDistributeEndorsementInfo, key)
            .then(data => {
              console.log(data);
              window.open(data);
            });
          break;
        // 
        case "electricPin":
          key = {
            businessNo: this.routeDate.businessNo || "454654564564",
            businessType: this.routeDate.type || "sfsdfsdf",
            userName: "123",
            userCode: "123",
            comCode: "13000000"
          };
          this.$fetch
            .post(this.HOST + this.$url.autoDistributeElectricPinChannels, key)
            .then(data => {
              console.log(data);
              window.open(data);
            });
          break;
        // 
        case "traditional":
          key = {
            businessNo: this.routeDate.businessNo || "454654564564",
            businessType: this.routeDate.type || "sfsdfsdf",
            userName: "123",
            userCode: "123",
            comCode: "13000000"
          };
          this.$fetch
            .post(this.HOST + this.$url.autoDistributeTraditionalChannels, key)
            .then(data => {
              console.log(data);
              window.open(data);
            });
          break;
        case "lastYearPolicyInfo":
          key = {
            lastPolicyNo: "123",
            riskCode: "122"
          };
          this.$fetch
            .get(this.HOST + this.$url.uwmainGetLastYearPolicyInfo, {
              params: key
            })
            .then(data => {
              window.open(data);
            });
          break;
      }
    },

    // 
    openAdjustRateDialog(row) {
      this.adjustRateDialog = true;
    }
  },
  created() {
    //collapse
    this.routeDate = this.$route.query;
    setTimeout(() => {
      this.setActiveNames();
      this.init();
    });
  }
};
</script>
