import axios from "axios"
import {Message}  from 'element-ui'
export default {
    //
   stringToArray(string){
        if(!( typeof(string) =='string')){
           return string
       }
       if(string==''){
           return []
       }
       return string.split(',')
   },
   //
   arrayToString(array){
       if(!(array instanceof Array)){
           return array
       }
       if(array==undefined){
           return ''
       }
    if(array==''||array.length==0){
        return ''
    }
    return array.join(',')
   },
   //
   checkMaxContractMonths(string){
       var numReg = new RegExp(/^[1-9]\d*$/)
       if(string != ''){
           if(numReg.test(string)){
               return string
           } else {
               return ""
           }
       }
   },
 
   // 
   endorTypes:['01','02','08','09','15','16','19','65','60'],
   //
   autoPayName(payeeType){
       var proposals = ['022','023'] //   
       var agents = ['035','036','039','040'] //   
       if(proposals.indexOf(payeeType)>-1){
          return '02'
       }
       if(agents.indexOf(payeeType)>-1){
        return '03'
       }
       return '01'

    },
    //
    compareDate(date,date2){
        date = date.replace(/-/g, '/')
        date2 = date2.replace(/-/g, '/')
       if(new Date(date) > new Date(date2)){
           return true
       }else{
           return false
       }
    },
    
    //jsonpost
    encodeSearchParams(obj) {
        const params = []
      
        Object.keys(obj).forEach((key) => {
          let value = obj[key]
          // undefined
          if (typeof value === 'undefined') {
            value = ''
          }
          // 
          params.push([key, encodeURIComponent(value)].join('='))
        })
      
        return params.join('&')
    },
    /**
     * 
     * @param s 
     * @param n 
     */
    formatMoney(s,n){
        if(s == '' || !s){
            return '0.00'
        }
        if(typeof(s) == 'number'){
            s = s.toString()
        }
        n = n > 0 && n < 10 ? n : 2
        var j = ''
        if(s.substr(0,1) != '-'){
            j = ''
        }else{
            j = '-'
            s = s.substr(1,s.length-1)
        }
        s = parseFloat((s + "").replace(/[^\d\.-]/g, "")).toFixed(n) + "";
        var l = s.split(".")[0].split("").reverse(), r = s.split(".")[1];
        var t = "";
        for (var i = 0; i < l.length; i++) {
            t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? "," : "");
        }
        return j + t.split("").reverse().join("") + "." + r;
    },
    //url  shiqian
    addPass(d){
       return window.btoa(window.encodeURIComponent(JSON.stringify(d)))
    },
    //url  shiqian
    getPass(d){
        return JSON.parse(window.decodeURIComponent(window.atob(d)))

    },
    /**
     * axios post  
     * params1  url  
     * params  data 
     */
    axiosDown(url,data){
        axios({
            // axiospost
            method: "post",
            headers: {
                // token: window.localStorage["token"]
                // 'Content-Type': 'application/x-www-form-urlencoded'
            },
            url:url, // 
            data: data, // 
            responseType: "blob" // 
        }).then(res => {
            // 
  
            var tempfileName = window.decodeURI(res.headers['content-disposition'].split(';')[1])
            var fileName =tempfileName.split('=')[1]
            const content = res.data;
            const blob = new Blob([content]);
            //edge   if ("download")edgeie
            var userAgent = navigator.userAgent; //userAgent
            if(userAgent.indexOf("Edge") > -1) {
                navigator.msSaveBlob(blob, fileName);
                return
            }
            if ("download" in document.createElement("a")) {
                // IE
                const elink = document.createElement("a");
                elink.download = fileName;
                elink.style.display = "none";
                elink.href = URL.createObjectURL(blob);
                document.body.appendChild(elink);
                elink.click();
                URL.revokeObjectURL(elink.href); // URL 
                document.body.removeChild(elink);
            } else {
                // IE10+
                navigator.msSaveBlob(blob, fileName);
            }
        });
      },
   
    downloadReport(dataUrl){
        //   let date = item.plans[this.daysIndex[index]]
        //   let url = '/Ecp.Export.exportXls.jdn?entityId='+item.FId+'&date='+date.FDeparture_date+'&token=' + sessionStorage.getItem("token")
        axios({
            method:'get',
            url:dataUrl,
            headers:[],
            responseType:'blob',
          })
          .then((data) => {
            if (!data) {
                return
            }
            debugger
            let url = window.URL.createObjectURL(data.data)
            let link = document.createElement('a')
            link.style.display = 'none'
            link.href = url
            link.setAttribute('download', 'excel.xls')
            document.body.appendChild(link)
            link.click()
          })
    },
    //   token
    getUrlToken(name){
        var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
        var r = window.location.search.substr(1).match(reg);
        if(r!=null)return  unescape(r[2]); return null;
      
    },
    //  token
    removeToken(){
        sessionStorage.removeItem("token")
    },
    // 
    dateLimit(start,end,days){
        let date =  (new Date(end) - new Date(start))/ (1000 * 60 * 60 * 24)
        if (date < 0) {
            Message.error('');
            return false
          } 
          if (date > days) {
            Message.error('' + days +'');
            return false
          }
    }
    
    
}