      parameter: {},
      activeNames: [],
      carAuditPage: {
        notions: [], // 
        basicInfoVO: {}, // 
        beforeInfo: {}, //  
        afterInfo: {} // 
      },
      tableList: [],
      time1: "",
      value: "",
      form: {},
      rules: {},
      radio: 1,
      outerVisible: false,
      innerVisible: false,
      textarea1: "",
      textarea2: "",
      subOptions: [] // 
    };
  },

  methods: {
    //collapse
    setActiveNames() {
      for (let i = 1; i <= 23; i++) {
        this.activeNames.push(JSON.stringify(i));
      }
    },
    // 
    submitModification() {
      let key = {
        businessNo: this.parameter.businessNo,
        businessType: this.parameter.businessType,
        usercode: "A000"
      };
      this.$fetch.post(this.HOST + this.$url.saveUwPayee, key).then(data => {
        console.log(data);
        this.subOptions = data.selectPath;
        this.outerVisible = true;
      });
    },
    goback(){
        this.$router.go(-1)
    },
    // 
    submitReview(){
      let key ={
        businessNo: this.parameter.businessNo || '123213'
      }
      this.$fetch.post(this.HOST + this.$url.undwrtSubmitReview,key).then(data =>{
        this.innerVisible = true
      })
    },
    // 
    giveUp() {
      let key = {
        businessNo: this.parameter.businessNo,
        businessType: this.parameter.businessType,
        usercode: "A000"
      };
      this.$fetch.post(this.HOST + this.$url.giveUpUwPayee, key).then(data => {
        console.log(data);
        this.$message.success(data);
        setTimeout(() => {
          this.goback()
        },1500)
      });
    },
    BusinessNum(row) {
      // console.log(row)
      this.$router.push({ path: "/carContrast", query: { row: row.flag } });
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    selectCode() {
      this.flagCode = true;
    },
     // 
    goTolinks(type){
      let key ={}
      switch(type){
          // 
          case 'uploadECM':
             key = {
              "businessNo": this.parameter.businessNo ||  "454654564564",
              "businessType": this.parameter.type  || "sfsdfsdf",
              "taskId": "123",
              "userName": "123",
              'userCode': '123',
              "comCode": "13000000"
              }

          this.$fetch.post(this.HOST + this.$url.uwmainUploadECM,key).then(data => {
            console.log(data)
            window.open(data)
          })
          break
           // 
          case 'getECM':
             key = {
              "businessNo": this.parameter.businessNo ||  "454654564564",
              "businessType": this.parameter.type  || "sfsdfsdf",
              "taskId": "sdsd",
              "userName": "123",
              'userCode': '123',
              "comCode": "13000000",
              "SaleImgFlag": '123'
              }

          this.$fetch.post(this.HOST + this.$url.uwmainGetECM,key).then(data => {
            console.log(data)
            window.open(data)
          })
          break
         
      }

    },
    init() {
      let key = {
        businessNo: this.parameter.businessNo || "ST22232a2232",
        businessType: this.parameter.businessType || "H",
        comCode: "32100322",
        userCode: "A2322"
      };
      this.$fetch
        .post(this.HOST + this.$url.payeeGetUwPayeeAllInfo, key)
        .then(data => {
          console.log(data);
          this.carAuditPage = data;
        });
    }
  },

  created() {
    //collapse
    this.setActiveNames();
    this.parameter = this.$route.query;
    console.log(this.parameter);
    setTimeout(() => {
      this.init();
    });
  }
};
</script>
