<script>
import { mapActions, mapGetters } from "vuex";
import { setTimeout } from 'timers';

export default {
  name: "underwritingTeamDetails",
  data() {
    return {
      parameter: {
        type:'POLICY'
      },
      routeDate: '',
      activeNames: [],
      form: {},
      subOptions: [],
      underwritingDetails: {
        taskProcessing: {}, //
	      flowRecords: {}, //
        underwriteOpinions:[], // 	
	      batchMain: {}, // 
        batchVehicles: [], // 
        uwNotion: {}, // 
        displayFlag:{ 
          browseFlag: '0', // 
          flowRecordFlag: '0', //	
          ClaimFlag: '0', //	
          infoFlag: '0', //	
          uploadImageFlag: '0', //	
          revokeFlag: '0', //	
          possessTaskFlag: '0',	// 
          approvalInfoFlag: '0', //	


        },
      },
      value: "",
      //  ---
      uwNotionFlags: [
        { value: '1', label: ''},
        { value: '2', label: ''},
        { value: '3', label: ''},
      ],
      //  ---
      uwNotionCarCheckStatus:[
        { value: '1', label: ''},
        { value: '2', label: ''},
        { value: '3', label: ''},
        { value: '4', label: ''},
        { value: '5', label: ''},

      ],
      tableData: [], //   
      outerVisible: false,
      innerVisible: false,
      adjustRateDialog: false, //  
      textarea1: "",
      textarea2: "",
      options: [
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        }
      ]
    };
  },

  methods: {
    //collapse
    setActiveNames() {
      for (let i = 1; i <= 23; i++) {
        this.activeNames.push(JSON.stringify(i));
      }
    },
    init() {
      let  keyWords ={
        request: 'INFO', // 
        businessNo: this.routeDate.businessNo, // 
        // taskType: this.routeDate.type, // 
        // taskId: this.routeDate.businessNo, //  Id
        businessType: this.routeDate.type, // businessType
        comCode: 'NSA12312', // 
        userCode: this.routeDate.businessNo, // 
      }
     this.$fetch.post(this.HOST + this.$url.uwmainGetUwInfo, keyWords).then(data =>{
      //  this.underwritingDetails = data
      //  this.underwritingDetails.displayFlag = {}
     })
    },
    goback(){
        this.$router.go(-1)
    },
     // 
    giveUp() {
      let key = {
        businessNo: this.parameter.businessNo,
        businessType: this.parameter.businessType,
        usercode: "A000"
      };
      this.$fetch.post(this.HOST + this.$url.giveUpUwPayee, key).then(data => {
        console.log(data);
        this.$message.success(data);
        this.goback()
      });
    },
    // 
    submit(){
      // debugger
      let key = {
        businessNo:  12321,
        businessType: 'H',
        usercode: "A000"
      };
      this.$fetch.post(this.HOST + this.$url.saveUwPayee, key).then(data => {
        console.log(data);
        // this.subOptions = data.selectPath;
        this.outerVisible = true;
      });
      // this.outerVisible= true
    },
    // 
    submitReview(){
      let key ={
        businessNo: this.routeDate.businessNo || '123213'
      }
      this.$fetch.post(this.HOST + this.$url.undwrtSubmitReview,key).then(data =>{
        this.innerVisible = true
      })
    },
    // 
    getBack(){
       let keyWords ={
          businessNo: this.routeDate.businessNo || 'AST12312312',
          ComCode: this.routeDate.businessNo || 'BJ233',
          UserCode: this.routeDate.businessNo || 'WPC212',
          UserName: this.routeDate.businessNo || '',
          revokeType: this.routeDate.type || 'EH', //  1  2  3
          taskId: this.routeDate.businessNo || 'id12312', // id
          businessType: this.routeDate.type || 'ST',
          batchNo: this.routeDate.type || 12312312,
       }
      this.$fetch.post(this.HOST + this.$url.undwrtrevokeUndwrt ,keyWords).then(data => {
        console.log(data)
        // this.$message.console.error(data);

      })
    },
   // 
    goToFlowLog(){
      let routeUrl = this.$router.resolve({
          path: "/flowLog",
           query: {
          businessNo: '123'
        }
      })
      window.open(routeUrl.href, '_blank')
    },
    // 
     goTolinks(type){
      switch(type){

        case 'uploadECM':
            let key = {
              "businessNo": this.routeDate.businessNo ||  "454654564564",
              "businessType": this.routeDate.type  || "sfsdfsdf",
              "taskId": "123",
              "userName": "123",
              'userCode': '123',
              "comCode": "13000000"
              }

          this.$fetch.post(this.HOST + this.$url.uwmainUploadECM,key).then(data => {
            console.log(data)
            window.open(data)
          })
          break
          // 
          case 'details':
            let details = {
              "businessNo": this.routeDate.businessNo ||  "454654564564",
              "businessType": this.routeDate.type  || "sfsdfsdf",
              "taskType": "sdsd",
              "taskId": "123",
              "comCode": "13000000"
              }

          this.$fetch.post(this.HOST + this.$url.telSaleInfo,details).then(data => {
            console.log(data)
            // window.open("http://www.baidu.com")
            window.open(data)
          })
          break
      }

    },
    // 
    goToFlowLog(){
      let routeUrl = this.$router.resolve({
          path: "/flowLog",
           query: {
          businessNo: '123'
        }
      })
      window.open(routeUrl.href, '_blank')
    },
    // 
    openAdjustRateDialog(row){
      this.adjustRateDialog = true
    },
    handleSelectionChange(){

    }
  },
  
  

  created() {
    //collapse
    this.routeDate = this.$route.query;
    setTimeout(()=> {
      this.setActiveNames();
      this.init();
    }) 
   
    
  }
};
