<template>
  <div>
    <!--  -->
    <el-card class="circular">
      <el-collapse v-model="activeNames">
        <el-collapse-item name="1">
          <template slot="title">
            <div class="title-blue-bar"></div>
            <div class="card-title"></div>
          </template>
          <el-form
            :disabled="flag == 'detail'"
            ref="UwctrlVO"
            :rules="rules"
            :model="UwctrlVO"
            label-width="150px"
          >
            <el-row>
              <el-row>
                <el-col :span="8">
                  <el-form-item label=":" prop="insuredFlag">
                    <el-select v-model="UwctrlVO.insuredFlag" placeholder="">
                      <el-option
                        v-for="item in relationsss"
                        :key="item.label"
                        :label="item.value1"
                        :value="item.label"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":" prop="applicCode">
                    <el-input
                      v-model="UwctrlVO.applicCode"
                      type="text"
                      maxlength="16"
                      minlength="16"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":" prop="insuredName">
                    <el-input v-model="UwctrlVO.insuredName"></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="8">
                  <el-form-item label=":" prop="businessNo">
                    <el-input
                      v-model="UwctrlVO.businessNo"
                      type="text"
                      maxlength="22"
                      minlength="22"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":" prop="contractNo">
                    <el-input
                      v-model="UwctrlVO.contractNo"
                      type="text"
                      maxlength="22"
                      minlength="22"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":">
                    <el-input v-model="UwctrlVO.profitRateUp"></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="8" v-if="flag != 'detail'">
                  <el-form-item label=":" prop="valid">
                    <el-select v-model="UwctrlVO.valid" clearable placeholder="">
                      <el-option
                        v-for="item1 in flagss"
                        :key="item1.label"
                        :label="item1.value"
                        :value="item1.label"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":" prop="flag">
                    <el-select v-model="UwctrlVO.flag" clearable placeholder="">
                      <el-option
                        v-for="item in categoryss"
                        :key="item.label"
                        :label="item.value"
                        :value="item.label"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8" v-if="flag != 'detail'">
                  <el-form-item label=":" prop="finishDate">
                    <el-date-picker
                      value-format="yyyy-MM-dd"
                      v-model="UwctrlVO.finishDate"
                      type="date"
                      placeholder=""
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="8">
                  <el-form-item label=":">
                    <el-input v-model="UwctrlVO.costRateUpper"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":">
                    <el-input v-model="UwctrlVO.message"></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label=":">
                    <el-input v-model="UwctrlVO.costRateBIUpper"></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="8" v-if="flag == 'change'">
                  <el-form-item label=":" prop="licenseNo">
                    <el-input v-model="UwctrlVO.licenseNo"></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-col :span="24" class="text-center">
                <el-button size="mini" @click="save()" v-if="flag != 'detail'" type="primary"></el-button>
                <el-button size="mini" @click="goBack()"></el-button>
              </el-col>
            </el-row>
          </el-form>
        </el-collapse-item>
      </el-collapse>
    </el-card>
    <!-- dialog -->
    <el-dialog
      title=""
      :visible.sync="outerVisible"
      width="50%"
      class="dialog-footer-parent"
      :before-close="handleClose"
    >
      <span class="fontSizeTrue"></span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="updateyes"> </el-button>
        <el-button @click="outerVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { relations } from "@/assets/js/baseCode";
export default {
  name: "tpl_undcorrection",
  watch: {
    "UwctrlVO.businessNo": function(newVal, oldVal) {
      let oldValue = oldVal ? oldVal.length : 0;
      if (newVal.length && newVal.length < oldValue) {
        this.rules.businessNo.required = false;
      } else if (newVal) {
        this.rules.businessNo.required = true;
      }
    },
    "UwctrlVO.contractNo": function(newVal, oldVal) {
      let oldValue = oldVal ? oldVal.length : 0;
      if (newVal.length && newVal.length < oldValue) {
        this.rules.contractNo.required = false;
      } else if (newVal) {
        this.rules.contractNo.required = true;
      }
    }
  },
  props: {
    flag: {
      type: String
    }
  },
  data() {
    return {
      rules: {
        insuredFlag: [
          { required: true, message: "", trigger: ["blur"] }
        ],
        applicCode: [
          { required: true, message: "16", trigger: ["blur"] },
          {
            min: 16,
            max: 16,
            message: "16",
            trigger: ["change", "blur"]
          }
        ],
        insuredName: [
          { required: true, message: "", trigger: ["blur"] }
        ],
        valid: [{ required: true, message: "", trigger: ["blur"] }],
        flag: [{ required: true, message: "", trigger: ["blur"] }],
        finishDate: [
          { required: true, message: "", trigger: ["blur"] }
        ],
        licenseNo: [
          { required: true, message: "", trigger: ["blur"] }
        ],
        businessNo: [
          { required: false, trigger: ["change", "blur"] },
          {
            min: 22,
            max: 22,
            message: "22",
            trigger: ["change", "blur"]
          }
        ],
        contractNo: [
          {
            required: false,
            message: "",
            trigger: ["change", "blur"]
          },
          {
            min: 22,
            max: 22,
            message: "22",
            trigger: ["change", "blur"]
          }
        ]
      },
      relationsss: [
        { value1: "1_", label: "1" },
        { value1: "2_", label: "2" }
      ],

      categoryss: [
        { value: "1_", label: "1" },
        { value: "2_", label: "2" },
        { value: "3_", label: "3" }
      ],

      rtReported: {},
      flagss: [
        { value: "0_", label: "0" },
        { value: "1_", label: "1" }
      ],
      relations,
      activeNames: ["1"],
      UwctrlVO: {},
      results: [],
      form: {},
      outerVisible: false
    };
  },

  computed: {},
  methods: {
    //
    goBack() {
      this.$router.go(-1);
    },
    handleClose(done) {
      console.log("");
    },
    // 
    open2() {
      this.$message({
        message: "",
        type: "success"
      });
    },
    //
    updateyes() {
      this.outerVisible = false;
      this.$refs.UwctrlVO.validate(valids => {
        let uwctrlVO = this.UwctrlVO;
        this.$fetch
          .post(this.HOST + this.$url.correctionUpdate, uwctrlVO)
          .then(res => {
            setTimeout(() => {
              this.open2();
              this.$router.push({
                path: "/queryCorrection"
              });
            }, 2000);
          });
      });
    },
    // 
    save() {
      this.outerVisible = true;
    }
  },
  created() {
    // 
    this.$fetch
      .get(this.HOST + this.$url.correctionShow, {
        params: { id: this.$route.query.row }
      })
      .then(res => {
        this.UwctrlVO = res;
      });
  }
};
</script>
<style scoped>
.fontSizeTrue {
  font-size: 25px;
}
.dialog-footer-parent >>> .el-dialog__footer {
  text-align: center;
}
</style>
