<template>
  <div>
    <!--  -->
    <el-card class="circular">
      <el-collapse v-model="activeNames">
        <el-collapse-item name="1">
          <template slot="title">
            <div class="title-blue-bar"></div>
            <div class="card-title"></div>
          </template>
          <el-form ref="rtReportedFrom" :model="rtReported" label-width="150px" :rules="rtReportedRules">
            <el-row>
              <el-col :span="8">
                <el-form-item label=":" prop="userName">
                  <el-input v-model="rtReported.userName" :disabled="this.routeType === 'READY'"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=":" prop="userCode">
                  <el-input v-model="rtReported.userCode" :disabled="true"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=":" prop="vaild">
                  <el-select :disabled="this.routeType === 'READY'" v-model="rtReported.vaild" clearable placeholder="">
                    <el-option
                      v-for="relation in flags"
                      :key="relation.value"
                      :label="relation.label"
                      :value="relation.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              
              <el-col :span="8">
                <el-form-item label=":" prop="mailbox">
                  <el-input :disabled="this.routeType === 'READY'" v-model="rtReported.mailbox"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=":">
                  <el-select :disabled="this.routeType === 'READY'" v-model="rtReported.autoDistributeFlag" clearable placeholder="">
                    <el-option
                      v-for="(relation,index) in relations"
                      :key="index"
                      :label="relation.label"
                      :value="relation.value"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=":" prop="iphone">
                  <el-input :disabled="this.routeType === 'READY'" v-model="rtReported.phonecode"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=":" prop="autoPushNum">
                  <el-input :disabled="this.routeType === 'READY'" v-model="rtReported.autoPushNum"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label=":">
                  <el-input :disabled="this.routeType === 'READY'" v-model="rtReported.telephone"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24" class="text-center" v-if="this.routeType !== 'READY'">
                <el-button size="mini" @click="save"></el-button>
              </el-col>
            </el-row>
          </el-form>
        </el-collapse-item>
      </el-collapse>
    </el-card>
    <!--  -->
    <!-- <el-dialog title="" class="dialog" :visible.sync="outerVisible" width="30%">
      <span class="fontSize">???</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="outerVisible = false"> </el-button>
        <el-button @click="outerVisible = false"> </el-button>
      </span>
    </el-dialog>-->
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { relations } from "@/assets/js/baseCode";
// let [inputValidator, selectValidator] = [
//   { required: true, message: "", trigger: "blur" },
//   // { required: true, message: "", trigger: "change" }
// ];

export default {
  name: "addwriterInfor",
  data() {
    var pushNum =/^([1-9]|10)$/
    var autoPushNumRolus =(rule, value, callback) =>{
      if(!value){
        callback(new Error('!'));
      } else if(!pushNum.test(Number(value))) {
        callback(new Error('110!'));
      } else{
        callback()
      }
      // if(pushNum.test()){

      // }
    }
    return {
      formData: {},
      routeType: '',
      outerVisible: false,
      activeNames: ["1"],
      flags: [{ value: "0_" }, { value: "1_" }],
      relations: [{ value: "0_" }, { value: "1_" }],
      rtReported: {
        userCode: this.$route.query.userCode
      },
      pageSize: 10,
      underwriterInfor: {},
      rtReportedRules:{
        userName:[ { required: true, message: '', trigger: 'blur' }],
        vaild:[ { required: true, message: '', trigger: 'change' }],
        userCode:[ { required: true, message: '', trigger: 'blur' }],
        mailbox:[ { required: true, message: '', trigger: 'blur' }],
        autoPushNum:[ {required: true, validator: autoPushNumRolus, trigger: 'blur'} ]

      }
    };
  },

  computed: {
    ...mapGetters(["getList"])
  },

  methods: {
    ...mapActions(["getunderwriterInfor"]),

    query() {
      this.getunderwriterInfor(this.underwriterInfor);
    },
    save() {
      this.$refs.rtReportedFrom.validate((valid) => {
          if (valid) {
            // 
            if (tthis.routeType === 'ADD'){
              this.addCheker()
            } else if(this.routeType === 'CHANGE'){
              this.updateCheker()
            }
          } else {
            
            return false;
          }
      });
    
      // if (1) {
      //   this.$confirm("~", "", {
      //     confirmButtonText: "",
      //     cancelButtonText: "",
      //     type: "warning"
      //   })
      //     .then(() => {
      //       this.$message({
      //         type: "success",
      //         message: "!"
      //       });
      //     })
      //     .catch(() => {
      //       this.$message({
      //         type: "info",
      //         message: ""
      //       });
      //     });
      // }
    },
    //  
    addCheker() {
      this.$fetch.post(this.HOST + this.$url.underWriterInforAddcheker,this.rtReported).then(data =>{
          console.log(data)
      })
    },
    // 
    updateCheker(){
      this.$fetch.post(this.HOST + this.$url.underWriterInforUpdateCkecker,this.rtReported).then(data =>{
          console.log(data)
      })
    },
    handleSizeChange(val) {
      this.task.tab1 = val;
    },

    handleCurrentChange(val) {
      this.task.tab2 = val;
    },
     //  
    getCheckerInfo(userCode){
      this.$fetch.post(this.HOST + this.$url.underWriterInforQueryCkecker,{'usercode':userCode }).then(data =>{
        this.rtReported = data
      })
    }
  },
  created() {
    // this.ruleForm();
    console.log(this.$route)
    this.routeType = this.$route.query.type
    switch( this.routeType) {
      case 'CHANGE':
      case 'READY':
        this.getCheckerInfo(this.$route.query.userCode)
        break 
      case 'ADD' :
        this.webPostion = ''
      break
    }
  }
};
</script>
<style scoped>
.fontSize {
  font-size: 20px;
}
.dialog >>> .el-dialog__footer {
  text-align: center;
}
</style>
